.container {
    height: calc(100vh - 100px);
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
}

.inner-container {
    display: flex;
    flex-flow: row;
    align-items: center;
    justify-content: center;
}

.icon > svg {
    display: flex;
    width: 100px;
    height: 100px;
}

.text-shadow {
    text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}