.card {
    position: relative;
    display: flex;
    flex: 0 0 46%;
    flex-flow: column;
    align-items: center;
    justify-content: flex-start;
}

.card-image-container {
    display: flex;
    justify-content: center;
}

.card-image-container:hover {
    cursor: pointer;
}

.card-image {
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
}
.card-name {
    text-align: center;
    text-shadow: 0 0 6px rgb(51 51 255 / 40%),
    0 0 4px rgb(51 51 255 / 40%), 0 4px 4px rgb(51 51 255 / 75%);
}

.card-price {
    font-family: "Iceland";
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 28px;
    line-height: 24px;
    text-shadow: 0 0 6px rgb(51 51 255 / 40%),
    0 0 4px rgb(51 51 255 / 40%), 0 4px 4px rgb(51 51 255 / 75%);
}

.values {
    display: grid;
    grid-template-columns: repeat(4, 120px);
    gap: 20px;
}